  padding: 0;
  margin: 0;
}
 
body {
  font-family: 'Poppins', sans-serif;
  background-color: #F7F7F7;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

a,
button,
input,
input[type='text'],
textarea {
  min-width: 44px;
  min-height: 44px;
}

.skip-link {
  position: absolute;
  top: -60px;
  left: 0;
  padding: 8px;
  background-color: #bf1722;
  color: white;
  z-index: 100;
}

.skip-link:focus {
  top: 0;
}
 
 
/*
  AppBar
*/
 
.app-bar {
  padding: 8px 16px;
  background-color: #F7F7F7;
  display: grid;
  grid-template-columns: auto 1fr auto;
  gap: 10px;
  position: sticky;
  top: 0;
  z-index: 99;
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.2);
}
 
.app-bar .app-bar__menu {
  display: flex;
  align-items: center;
}
 
.app-bar .app-bar__menu button {
  background-color: transparent;
  border: none;
  font-size: 18px;
  padding: 8px;
  cursor: pointer;
}
 
.app-bar .app-bar__brand {
  display: flex;
  align-items: center;
}
 
.app-bar .app-bar__brand h1 {
  color: #393E46;
  text-transform: uppercase;
  font-size: 22px;
  user-select: none;
}
 
.app-bar .app-bar__navigation {
  position: absolute;
  top: 50px;
  left: -180px;
  width: 150px;
  transition: all 0.3s;
  padding: 10px;
  background-color: #F7F7F7;
  overflow: hidden;
}
 
.app-bar .app-bar__navigation.open {
  left: 0;
}
 
.app-bar .app-bar__navigation ul li a {
  display: inline-block;
  text-decoration: none;
  color: #393E46;
  padding: 0 8px;
  width: 100%;
  font-size: 1.6em;
}

.app-bar .app-bar__navigation a:hover {
  text-decoration: underline;
  color: #929AAB;
}


/*
 * Jumbotron
 */

.hero-img {
  position: relative;
}

.hero-img picture {
  display: block;
  width: 100%;
  min-height: 425px;
}

.hero-img picture img {
  width: 100%;
  height: 425px;
  display: block;
}

.hero {
  position: absolute;
  top: 80px;
  display: flex;
  align-items: center;
  min-height: 425px;
  width: 100%;
  text-align: center;
}

.hero__inner {
  margin: 0 auto;
  max-width: 100%;
}

.hero__title {
  color: #F7F7F7;
  font-weight: 600;
  font-size: 38px;
}

.hero__tagline {
  color: #F7F7F7;
  margin-top: 16px;
  font-size: 20px;
  font-weight: 400;
}
 

/*
  Main Content
*/
 
main {
  padding: 32px;
  flex: 1;
  background-color: #EEEEEE;
}
 
.content {
  margin: 0 auto;
  min-height: 100%;
}
 
.content .content__heading {
  font-weight: normal;
}
 
 
/*
  Resto
*/
 
.restos {
  display: grid;
  grid-template-columns: 1fr;
  gap: 16px;
  margin: 32px 0;
}
 
 
/*
  Resto Item
*/
 
.resto-item {
  width: 100%;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  border-radius: 8px;
  overflow: hidden;
  background-color: #F7F7F7;
}
 
.resto-item__header {
  position: relative;
}
 
.resto-item .resto-item__header .resto-item__header__poster {
  width: 100%;
}
 
.resto-item .resto-item__header .resto-item__header__rating {
  position: absolute;
  padding: 8px;
  bottom: 20px;
  left: 0;
  display: inline-block;
  background-color: #393E46;
  color: #EEEEEE;
}
 
.resto-item .resto-item__header .resto-item__header__rating .resto-item__header__rating__score {
  margin-left: 10px;
}
 
.resto-item .resto-item__content {
  padding: 16px;
}
 
.resto-item .resto-item__content h3 {
  margin: 0 0 4px 0;
}
 
.resto-item .resto-item__content h3 a {
  color: #393E46;
  text-decoration: none;
  font-size: 1.25em;
  padding: 10px 0;
}
 
.resto-item .resto-item__content p {
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 4; /* number of lines to show */
  -webkit-box-orient: vertical;
}
 
 
/*
  Resto
*/
 
.resto {
  margin: 0 auto;
  width: 100%;
  max-width: 625px;
  display: column;
  grid-template-columns: 1fr;
  gap: 18px 16px;
}
 
.resto .resto__poster {
  width: 100%;
  max-width: 600px;
  
}
 
.resto .resto__info h4 {
  margin: 8px 0;
}
 

/*
  Like
*/

.like {
  width: 55px;
  height: 55px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #db0000;

  position: fixed;
  bottom: 16px;
  right: 16px;
  border-radius: 50%;
  border: 0;

  font-size: 18px;
  color: white;
  cursor: pointer;
}
 

/*
  Footer
*/
 
footer {
  padding: 16px;
  background-color: #929AAB;
}
 
footer p {
  text-align: center;
  color: #393E46;
  font-size: 1em;
}
 
footer p a {
  color: #F7F7F7;
  text-decoration: none;
  padding: 9px 0;
